import {
    LOAD_USER_WALLET_SUCCESS,
    LOAD_USER_WALLET_FAIL,
} from '../actions/types';

const initialState = {
    email: '',
    userAddress: '',
    userTokens: '',
    froozenNLT: '',
    garuanteedAmt: '',
    borrowaleAmt: '',
};

export default function(state = initialState, action) {
    const { type, payload } = action;

    switch(type) {
        case LOAD_USER_WALLET_SUCCESS:
            return {
                ...state,
                email: payload.email,
                userAddress: payload.walletProfile.UserAddress,
                userTokens: payload.walletProfile.UserTokens,
                froozenNLT: payload.walletProfile.FroozenNLT,
                garuanteedAmt: payload.walletProfile.GaruanteedAmt,
                borrowaleAmt: payload.walletProfile.BorrowaleAmt,
            }
        case LOAD_USER_WALLET_FAIL:
            return {
                ...state,
                email: '',
                userAddress: '',
                userTokens: '',
                froozenNLT: '',
                garuanteedAmt: '',
                borrowaleAmt: '',
            }
        default:
            return state
    };

};