import Cookies from 'js-cookie';
import axios from 'axios';
import {
    LOAD_USER_WALLET_SUCCESS,
    LOAD_USER_WALLET_FAIL,
} from './types';

export const load_user_wallet = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        }
    };

    try {
        const res = await axios.get(`http://127.0.0.1:8000/wallet/user`, config);

        if (res.data.error) {
            console.log(res.data.error);
            dispatch({
                type: LOAD_USER_WALLET_FAIL
            });
        } else {
            dispatch({
                type: LOAD_USER_WALLET_SUCCESS,
                payload: res.data
            });
        }
    } catch (err) {
        console.log(err);
        dispatch({
            type: LOAD_USER_WALLET_FAIL
        });
    }


};