const bscProvider = "https://bsc-dataseed1.defibit.io/";
const ropstenProvider = "https://ropsten.infura.io/v3/59d7420ec6b34a128f884d1b36a5a064";
const ethProvider = "https://mainnet.infura.io/v3/59d7420ec6b34a128f884d1b36a5a064"
const providerUrl = ropstenProvider;
const web3 = new Web3(providerUrl); //Connect to Blockchain.

const userAddress = web3.utils.toChecksumAddress('0x5ef165719274290145d0AEe229e964331F5770b3'); 
const coinSwap_address = web3.utils.toChecksumAddress('0xC0D8a7d9520D3fdD85e9676D631730e7F762698E');

async function checks() {
  let contracData = ropsten.NLT[0];
  const nltAddress =web3.utils.toChecksumAddress(contracData.Address);
  const nltContract = new web3.eth.Contract(contracData.Abi, nltAddress)
  let nltSymbol = await nltContract.methods.symbol().call();
  let nltSwapBal = web3.utils.fromWei(await nltContract.methods.balanceOf(coinSwap_address).call(), 'ether'); 
  let ethBal = web3.utils.fromWei(await web3.eth.getBalance(coinSwap_address), 'ether');
  console.log(nltSwapBal,nltSymbol);
  console.log(ethBal, 'ETH');

  return nltSwapBal;
}





