let myArr = userTokens_global.split("/");
function splitTokenArray(val){
  let result = val.split("-");
  return result;
}

function getTokenDetails(tokenSymbol, _tokenChain){
  if(_tokenChain == 'ROSP'){
    let result = [];
    let tokenData = '';
    let userBal = 200;
    if(tokenSymbol == 'NLT'){
      tokenData = ropsten.NLT[0];
      let nltAddress =web3.utils.toChecksumAddress(tokenData.Address);
      let nltContract = new web3.eth.Contract(tokenData.Abi, nltAddress);
      let bal = nltContract.methods.balanceOf(userAddress_global).call();
      console.log(bal.Promise);
      //userBal = web3.utils.fromWei(await nltContract.methods.balanceOf(userAddress_global).call(), 'ether'); 

    }else if(tokenSymbol == 'ETH'){
      tokenData = ropsten.ETH[0];
      let nltAddress =web3.utils.toChecksumAddress(tokenData.Address);
      let nltContract = new web3.eth.Contract(tokenData.Abi, nltAddress);
      //userBal = web3.utils.fromWei(await nltContract.methods.balanceOf(userAddress_global).call(), 'ether'); 
    }
 
    result ={ tokenData, userBal, }
    return result;
    
  }else{
    return 'none';
  }
}


