import Cookies from 'js-cookie';
import axios from 'axios';
import { load_user } from './profile';
import { load_user_wallet } from './wallet';

import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL,
    AUTHENTICATED_SUCCESS,
    AUTHENTICATED_FAIL,
    DELETE_USER_SUCCESS,
    DELETE_USER_FAIL
} from './types';


export const checkAuthenticated = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    };

    try {
        const res = await axios.get(`http://127.0.0.1:8000/accounts/authenticated`, config);

        if (res.data.error || res.data.isAuthenticated === 'error') {
            console.log(res.data.error);
            dispatch({
                type: AUTHENTICATED_FAIL,
                payload: false
            });
        }
        else if (res.data.isAuthenticated === 'success') {
            dispatch({
                type: AUTHENTICATED_SUCCESS,
                payload: true
            });
        }
        else {
            console.log('Cannot determine if user is authenticated or not, try loggin out and Loggin in again.');
            dispatch({
                type: AUTHENTICATED_FAIL,
                payload: false
            });
        }
    } catch(err) {
        console.log('Something went wrong went trying to get your auntenticated data from sever');
        dispatch({
            type: AUTHENTICATED_FAIL,
            payload: false
        });
    }
};

export const login = (username, password) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({ username, password });

    try {
        const res = await axios.post(`http://127.0.0.1:8000/accounts/login`, body, config);

        if (res.data.success) {
            dispatch({
                type: LOGIN_SUCCESS,
            });

            dispatch(load_user());
            dispatch(load_user_wallet());
            
        } else {
            console.log('Log in failed');
            dispatch({
                type: LOGIN_FAIL
            });
        }
    } catch(err) {
        console.log(err);
        dispatch({
            type: LOGIN_FAIL
        });
    }
};

export const register = (email, username, password, re_password) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({ email, username, password, re_password });

    try {
        const res = await axios.post(`http://127.0.0.1:8000/accounts/register`, body, config);
        if (res.data.error) {
            console.log(res.data.error);
            dispatch({
                type: REGISTER_FAIL
            });
        } else {
            console.log(res.data.success);
            dispatch({
                type: REGISTER_SUCCESS
            });
        }
    } catch (err) {
        dispatch({
            type: REGISTER_FAIL
        });
    }
};


export const logout = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({
        'withCredentials': true
    });

    try {
        const res = await axios.post(`http://127.0.0.1:8000/accounts/logout`, body, config);

        if (res.data.success) {
            console.log(res.data.success);
            dispatch({
                type: LOGOUT_SUCCESS
            });
        } else {
            dispatch({
                type: LOGOUT_FAIL
            });
        }
    } catch(err) {
        console.log(err);
        dispatch({
            type: LOGOUT_FAIL
        });
    }
};


export const delete_account = () => async dispatch => {

    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({
        'withCredentials': true
    });

    try {
        const res = await axios.delete(`http://127.0.0.1:8000/accounts/delete`, config, body);

        if (res.data.success) {
            dispatch({
                type: DELETE_USER_SUCCESS
            });
        } else {
            console.log('Unable to delete user, Please try again or contact customers service');
            dispatch({
                type: DELETE_USER_FAIL
            });
        }
    } catch (err) {
        console.log(err);
        dispatch({
            type: DELETE_USER_FAIL
        });
    }
};