import React, {  useEffect, Fragment } from 'react';
import Header from './Header';
import { connect } from 'react-redux';
import { checkAuthenticated } from '../actions/auth';
import { load_user } from '../actions/profile';
import { load_user_wallet } from '../actions/wallet';

const Layout = ({ children, checkAuthenticated, load_user, load_user_wallet }) => {

  useEffect(() => {
    checkAuthenticated();
    load_user ();
    load_user_wallet();
}, []); 

  return (
    <>
    <Fragment>
      <Header />
      {children}
    </Fragment>
    </>
  );
}

export default connect(null, { checkAuthenticated, load_user, load_user_wallet })(Layout);